import Api from '@/js/api/baseApi'

export default class roleApi extends Api {
  static roleList (data, callback, errorBack) {
    Api.request(this.uri() + 'role_list', callback, errorBack, data)
  }

  static add (data, callback, errorBack) {
    Api.request(this.uri() + 'add', callback, errorBack, data)
  }

  static delete (data, callback, errorBack) {
    Api.request(this.uri() + 'delete', callback, errorBack, data)
  }

  static detail (data, callback, errorBack) {
    Api.request(this.uri() + 'detail', callback, errorBack, data)
  }

  static permissions (data, callback, errorBack) {
    Api.request(this.uri() + 'get_role_permission', callback, errorBack, data)
  }

  static assignRolePermission (data, callback, errorBack) {
    Api.request(this.uri() + 'assign_role_permission', callback, errorBack, data)
  }

  static uri () {
    return Api.url() + 'role/'
  }
}
